<template><section><h1>PointPrimitive</h1>
<p>The <code v-pre>vc-primitive-point</code> component is used to load a graphical point positioned in the 3D scene, that is created and rendered using a <code v-pre>vc-collection-primitive-point</code>.</p>
<h2>Example</h2>
<h3>Load points with <code v-pre>vc-primitive-point</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-point>
          <template v-for="(polyline, index) of polylines">
            <template v-for="(position, subIndex) of polyline.positions">
              <vc-primitive-point :position="position" :key="'point' + index + 'position' + subIndex" :color="colorPoint" :pixelSize="8"></vc-primitive-point>
            </template>
          </template>
        </vc-collection-primitive-point>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
